;;;;;;EAAA;AAOA;;EAEI,oBAAA;AAwVJ;;AArVA;EACI,sBAAA,EAAA,QAAA;AAwVJ;;AArVA,oEAAA;AACA;EACI,YAAA;AAwVJ;;AArVA;EACI,yBAAA,EAAA,QAAA;AAwVJ;;AArVA;EACI,wBAAA,EAAA,QAAA;AAwVJ;;AArVA;EACI;;;IAAA;EAIA,SAAA;AAwVJ;;AArVA;EACI,iBAAA;AAwVJ;;AArVA,mEAAA;AACA,mEAAA;AACA,mEAAA;AAEA;EACI,YAAA;AAuVJ;;AApVA;EACI,8BAAA;AAuVJ;;AApVA;EACI,OAAA;EACA,kBAAA;AAuVJ;;AApVA;EACI,uBAAA;EACA,gCAAA;EACA,iCAAA;EACA,eAAA;AAuVJ;;AApVA;EACI,sBAAA;EACA,iCAAA;AAuVJ;;AApVA;EACI,WAAA;EACA,YAAA;EACA,kBAAA;EACA,YAAA;AAuVJ;;AApVA;EACI,YAAA;AAuVJ;;AApVA;EACI,cAAA;AAuVJ;;AAnVA;EACI,kBAAA;EACA,sBAAA;EACA,wBAAA;EACA,eAAA;EACA,YAAA;AAsVJ;;AAlVA;EACI,WAAA;EACA,4BAAA,EAAA,SAAA;AAqVJ;;AAhVA;EACI;;;GAAA;AAsVJ;;AAhVA;EACI,WAAA;EACA,yBAAA;AAmVJ;;AA/UA;EACI,gBAAA;AAkVJ;;AA/UA;EACI,YAAA;AAkVJ;;AA/UA;EACI,WAAA;AAkVJ;;AA/UA;EACI,YAAA;AAkVJ;;AA3UA,mCAAA;AACA,mDAAA;AACA;EAAoB,gBAAA;AA+UpB;;AA9UA;EAAoB,gBAAA;AAkVpB","sourcesContent":["@import '../../../../app.component.scss';\n\n/* Query builder - This is still in the \"What exactly do we want this to do?\" phase,  */\n.query-builder-label {\n    color: #000;\n    margin-left: 15pt;\n    margin-bottom: 7pt;\n    position: relative;\n    top: 8px;\n    font-size: 12pt;\n}\n\n.query-builder-label2 {\n    color: #0d95a1;\n    margin-left: 15pt;\n    font: 15px arial, sans-serif;\n}\n\n.query-builder-panel {\n    z-index: 20;\n    background-color: #fff;\n    border-bottom: solid 10px #cfcfcf;\n    border-left: solid 1px #cfcfcf;\n    border-right: solid 1px #cfcfcf;\n    height: 1000px;\n}\n\n.query-builder-panel-heading {\n    color: #000;\n    margin-left: 15pt;\n    margin-bottom: 7pt;\n    position: relative;\n    top: 8px;\n    font-size: 12pt;\n}\n\n.query-builder-dropdown {\n    margin-left: 15pt;\n    margin-top: 7pt;\n}\n\n.selected-button {\n    background-color: aquamarine;\n}\n\n/* -------------------------------------------------------------- */\n/* -------------------------------------------------------------- */\n/* -------------------------------------------------------------- */\n\n/*------------------------------------*\\\n    $NAV\n\\*------------------------------------*/\n/**\n * Throw any `ul` or `ol` into horizontal mode, as per csswizardry.com/2011/09/the-nav-abstraction.\n   <ul class=nav>\n       <li><a href=/>Home</a></li>\n       <li><a href=/about>About</a></li>\n       <li><a href=/portfolio>Portfolio</a></li>\n       <li><a href=/contact>Contact</a></li>\n   </ul>\n */\n.nav {\n    position: relative;\n\n\n    list-style: none;\n    margin-left: 0;\n    cursor: default;\n}\n\n.nav > li,\n.nav > li > a {\n    display: inline-block;\n    *display: inline;\n    zoom: 1;\n}\n\n/**\n * Create a vertically stacked nav by extending `.nav` with `.stacked`.\n   <ul class=\"nav stacked\">\n       <li><a href=/>Home</a></li>\n       <li><a href=/about>About</a></li>\n       <li><a href=/portfolio>Portfolio</a></li>\n       <li><a href=/contact>Contact</a></li>\n   </ul>\n */\n.stacked > li {\n    display: list-item;\n}\n\n.stacked > li > a {\n    display: block;\n}\n\n/*------------------------------------*\\\n    $FLYOUT\n\\*------------------------------------*/\n\n.clear {\n    height: 0;\n    overflow: hidden;\n    display: none;\n}\n\n/**\n * Flyouts are pieces of content that fly out of a parent when said parent is hovered.\n * They typically appear bottom-left of the parent.\n   <div class=flyout>\n       Foo\n       <div class=flyout-content>\n           <h1>Lorem</h1>\n           <p>Ipsum</p>\n       </div>\n   </div>\n */\n.flyout,\n.flyout-alt {\n    position: relative;\n}\n\n.flyout-content {\n    /* Position the flyouts off-screen. This is typically better than `display:none;`. */\n    position: absolute;\n    top: 100%;\n    left: -99999px;\n    /**\n     * Even though they are out of document flow, lots of nested flyouts can\n     * eventually force scroll bars to appear as they become taller than the viewport.\n     * We can undo this effect by giving them zero height.\n     */\n    height: 0;\n    overflow: hidden;\n}\n\n/**\n * Bring the flyouts into view when you hover their parents.\n * Two different types of flyout; ‘regular’ (`.flyout`) and ‘alternative’ (`.flyout-alt`).\n */\n/* Regular flyouts sit all the way from the top, flush left. */\n.flyout:hover > .flyout-content {\n    left: 0;\n}\n\n/* Alternative flyouts sit all the way from the left, flush top. */\n.flyout-alt:hover > .flyout-content {\n    top: 0;\n    left: 100%;\n}\n\n.flyout:hover > .flyout-content,\n.flyout-alt:hover > .flyout-content {\n    /* Give the flyouts their height back once they come into view. */\n    height: auto;\n    overflow: visible;\n}\n\n/*------------------------------------*\\\n    $SITE-NAV\n\\*------------------------------------*/\n/**\n * Site nav specific styling.\n * Extends `.nav{}`\n */\n.site-nav {\n    z-index: 21;\n    font-size: 1.0em;\n    /*font-size:0.75em;*/\n    font-family: sans-serif;\n}\n\n.site-nav a {\n    line-height: 1;\n    padding: 0.75em;\n    /*padding:1em;*/\n    background-color: #09f;\n    color: #fff;\n    white-space: nowrap;\n}\n\n/*--- SITE-NAV FLYOUTS ---*/\n/**\n * Site nav specific flyouts, extension of `.flyout`.\n * Set up some styles to apply and persist when we hover things in the site nav.\n * This allows us to keep parents highlighted as we hover their contained flyouts [1].\n */\n.site-nav .flyout:hover > a /* [1] */,\n.site-nav .flyout-alt:hover > a /* [1] */,\n.site-nav a:hover {\n    color: #435704;\n    background-color: aquamarine;\n}\n\n/**\n * Add an indicator to any flyout parents in the site nav to show there’s a flyout.\n */\n.site-nav .flyout-alt > a:after {\n    content: \" »\";\n}\n\n/**\n * Here we set up a load of shared borders on the site-nav specific flyouts.\n * The code looks a little scattered but it allows us to cleverly share declarations:\n * We set up a non-existent [1] solid [2] border on all sides of the relevant elements.\n * We can now also change the style [2] and colour [3] of all borders in one go.\n * Now we have set up border colours and styles, we need to just turn the desired borders ‘on’ [4].\n */\n.site-nav a,\n.site-nav .flyout-content {\n    border: 0 /* [1] */ solid /* [2] */ #fff /* [3] */;\n}\n\n.site-nav > li > a {\n    border-left-width: 1px; /* [4] */\n}\n\n/* The first link in the site-nav doesn’t require a border at all. */\n.site-nav > li:first-child > a {\n    border: none;\n}\n\n.site-nav .flyout-content {\n    border-width: 1px 0 0 1px; /* [4] */\n}\n\n.site-nav .flyout-content a {\n    border-bottom-width: 1px; /* [4] */\n}\n\n.site-nav .flyout-alt:hover > .flyout-content {\n    /**\n     * To account for the 1px top border on the flyout containers, we need to\n     * bring the flyouts back up by 1px in this special instance.\n     */\n    top: -1px;\n}\n\n.top-element {\n    margin-left: 15pt;\n}\n\n/* -------------------------------------------------------------- */\n/* -------------------------------------------------------------- */\n/* -------------------------------------------------------------- */\n\n.criteria-name {\n    width: 100px;\n}\n\n.criteria-li {\n    border-bottom: solid white 1px;\n}\n\n.collapseAll {\n    left: 0;\n    border: solid 10px;\n}\n\n.clickable-color {\n    border-right: solid 1px;\n    border-color: #ff7e46 !important;\n    background-color: #14b !important;\n    cursor: pointer;\n}\n\n.clickable-color a:hover {\n    color: #fff  !important;\n    background-color: #a33  !important;\n}\n\n.user-input {\n    width: 100%;\n    height:48px;\n    overflow-y: scroll;\n    resize: none;\n}\n\n.user-input-add-button {\n    color: green;\n}\n\n.query-delete-button {\n    color: darkred;\n\n}\n\n.display-query-part {\n    border-radius: 4px;\n    border: solid #aaa 1px;\n    padding: 4px 6px 5px 5px;\n    font-size: 11pt;\n    margin:10px;\n\n}\n\n.display-query-menu-choice {\n    color: #000;\n    background-color: aquamarine; /*79ffd3*/\n\n\n}\n\n.display-query-menu-choice-list {\n    /*\n    margin-left: 5px;\n    margin-bottom: 10px;\n    */\n}\n\n.display-query-user-input {\n    color: #000;\n    background-color: #79d3ff;\n\n}\n\n.current-menu-criteria-display {\n    min-height: 72px;\n}\n\n.display-query-td{\n    height: 32px;\n}\n\n.display-query-table{\n    margin:0px;\n}\n\n.display-query-panel-body{\n    padding:1px;\n}\n\n.hide-panel{\n\n}\n\n/* FIXME this to over ride parent */\n/*Odd and even, but skipping the top row (Heading)*/\ntr:nth-child(even) {background: #fff}\ntr:nth-child(2n+3) {background: #fff}\n\n","$default-border-color: #cccccc;\n$default-border-hover-color: #adadad;\n$default-border-disabled-color: #d3d2d2;\n\n$default-background-color: #ffffff;\n$default-background-hover-color: #e6e6e6;\n$default-background-disabled-color: #f4f3f3;\n\n$default-height: 32px;\n$default-border-line-width: 1px;\n\n.btn-nbia-default {\n    border: solid $default-border-color $default-border-line-width;\n    background-color: $default-background-color;\n    height: calc(#{$default-height} + #{$default-border-line-width * 2 });\n\n    outline: none;\n    user-select: none;\n    box-shadow: none !important;\n    -webkit-touch-callout: none;\n    -webkit-user-select: none;\n    -moz-user-select: none;\n    -ms-user-select: none;\n}\n\n.btn-nbia-default:hover {\n    border: solid $default-border-hover-color $default-border-line-width;\n    background-color: $default-background-hover-color;\n}\n\n.btn-nbia-default:disabled {\n    border: solid $default-border-disabled-color $default-border-line-width;\n    background-color: $default-background-disabled-color;\n}\n\n// For buttons with just a larger image/icon Cine mode\n.btn-nbia-li-default {\n    @extend .btn-nbia-default;\n    /* T R B L */\n    padding: 0px 7px 1px 7px;\n    margin: 0 7px 0 7px;\n    max-height: 45px;\n}\n\n// For buttons with just an image/icon\n.btn-nbia-i-default {\n    @extend .btn-nbia-default;\n    /* T R B L */\n    padding: 2px 7px 1px 7px;\n    margin: 0 7px 0 7px;\n    max-width: 30px;\n    max-height: 25px;\n}\n\n.btn:disabled{\n    cursor: not-allowed;\n}\n\n\n\n// This for the \"Cart\" page\n.btn-nbia-i-cart {\n    @extend .btn-nbia-default;\n    /* T R B L */\n    padding: 2px 7px 1px 7px;\n    margin: 0 7px 0 7px;\n    max-width: 30px;\n    max-height: 25px;\n}\n\n.btn-nbia-i-cart:hover {\n    background-color: lightcoral;\n}\n\n","$card-nbia-header-height: 43px;\n$card-nbia-header-with-icon-height: 58px;\n\n$card-nbia-header-default-border-color: #bce8f1;\n$card-nbia-header-default-background-color: #d9edf7;\n$card-nbia-header-default-text-color: #347391;\n\n$card-nbia-header-danger-border-color: #ebccd1;\n$card-nbia-header-danger-background-color: #f2dede;\n$card-nbia-header-danger-text-color: #a94442;\n\n\n// Abstract\n.card-nbia-header {\n    padding-bottom: 0;\n    border-top-left-radius : 3px;\n    border-top-right-radius : 3px;\n}\n\n// Abstract\n.card-nbia-header-with-icon {\n    @extend .card-nbia-header;\n    height:  $card-nbia-header-with-icon-height;\n}\n\n.card-nbia-header-danger{\n    @extend .card-nbia-header-with-icon;\n    background-color: $card-nbia-header-danger-background-color;\n    color: $card-nbia-header-danger-text-color;\n    border-bottom: solid $card-nbia-header-danger-border-color 1px;\n}\n\n.card-nbia-danger{\n    border-color: $card-nbia-header-danger-border-color;\n    border-radius : 3px;\n}\n\n\n.card-nbia-header-default{\n    @extend .card-nbia-header;\n    height:  $card-nbia-header-height;\n    background-color: $card-nbia-header-default-background-color;\n    color: $card-nbia-header-default-text-color;\n    border-color: $card-nbia-header-default-border-color;\n    border-bottom: solid $card-nbia-header-default-border-color 1px;\n}\n\n.card-nbia-header-with-icon-default{\n    @extend .card-nbia-header-with-icon;\n    background-color: $card-nbia-header-default-background-color;\n    color: $card-nbia-header-default-text-color;\n    border-color: $card-nbia-header-default-border-color;\n    border-bottom: solid $card-nbia-header-default-border-color 1px;\n}\n\n.card-nbia-default{\n    border-color: $card-nbia-header-default-border-color;\n    border-radius : 3px;\n}\n\n","@import './btn-nbia';\n@import './card-nbia';\n\n$left-section-width: 390px;\n\n$query-criteria-heading-text-color: #bb0e3d;\n$disabled-text: #747474;\n$button-down-background: #dedddd;\n\n$dark-row-color: #f4f4f4;\n\n$alert-top: 100px;\n\n$close-x-red: #800;\n\n// $heading-height: 110px;\n$heading-height: 122px;\n\n// FIXME This only is used for the opener/closer icon\n$heading-margin-bottom: 8px; /* Space between menu bar and top of content */\n\n$header-background-color: #013856;\n\n$legacy-feedback-left: 182px;\n\n$top-logo-height: 41px;\n\n$banner-font-size: 20pt;\n$display-query-top: 10px;\n\n$popup-z-index: 21;\n$loading-screen-z-index: 5;\n$tool-tip-z-index: 2;\n$display-query-z-index: 0; // @TODO explain\n$pie-char-description-popup-z-index: 2;\n$missing-criteria-z-index: 3;\n$search-results-column-selector-z-index: 2;\n$intro-z-index: 3;\n$banner-z-index: 6;\n\n\n%panel-heading-text-color {\n    color: $query-criteria-heading-text-color;\n}\n\n%panel-heading {\n    @extend %panel-heading-text-color;\n    background-color: #fff;\n    font-size: 12pt;\n}\n\n%left-section {\n    width: $left-section-width;\n}\n\n\n.dark-row {\n    background-color: $dark-row-color;\n}\n\n.cart-child-selected {\n    background-color: #ff7 !important;\n}\n\n.redCart {\n    background-color: lightcoral;\n}\n\n.greenCart, .cart-selected {\n    background-color: aquamarine !important;\n}\n\n/* Cart button on Cart screen */\n.cart-button {\n    padding: 2px 7px 1px 7px;\n    margin: 0 7px 0 7px;\n}\n\n\n/* Trying to clean this up for Chrome */\n.no-outline {\n    outline: none;\n}\n\n.child-data-display {\n    background-color: #fff;\n    border: solid 1px #e7e7e7;\n\n    /*  Show some parent background */\n    margin: 8px 8px 16px 8px;\n\n    /* Child display */\n    padding: 6px;\n\n    /*           h-offset   v-o